
import { Stack, Pagination } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';


export default function Paging(props) {
  const { total, setPage, page } = props
  const handle_changePage = (e,newPage)=>{
    setPage(newPage)
    console.log("Selected Page:", newPage);
  }

  const theme = createTheme({
    components: {
      MuiPagination: {
        styleOverrides: {
          ul: {
            '& .MuiPaginationItem-root': {
              color: 'white', // Mu ca cc item
            },
            '& .MuiPaginationItem-root.Mui-selected': {
              backgroundColor: '#e4d804', // Mu nn ca item c chn
              color: '#000', // Mu ch ca item c chn
            },
            '& .MuiPaginationItem-root:hover': {
              backgroundColor: '#e4d804', // Mu nn khi hover
              color: '#000', // Mu ch khi hover
            },
          },
        },
      },
    },
  });
  return (
    <ThemeProvider theme={theme}>
      <Stack spacing={2} sx={{ color: 'white'}}>
        <Pagination count={total} page={page} onChange={handle_changePage} />
      </Stack>
    </ThemeProvider>
  );
}